// Breadcrumbs

#breadCrumb{
    clear:both;
    width:100%;
}

#breadCrumb ol li::before {
content: " ";
background-image: url('../../assets/v5-images/icons/svg/solid/chevron-right-gray.svg');
background-repeat: no-repeat;
background-position: bottom;
float: left;
top: 2px;
position: relative;
width: 17px;
height: 17px;
}

#breadCrumb ol li.home {
background:0 0;
padding:0;
}

#breadCrumb ol li.home::before {
display: none;
}

#breadCrumb {
margin-bottom: 20px !important;
background-color: $gray;
margin: 0;
float: left;
}

#breadCrumb ol {
list-style: none;
}

#breadCrumb ol li {
display: inline-block;
font-size: 1.1em;
margin: 0;
padding: 0;
}

#breadCrumb ol li a {
color: $hseblack;
text-decoration: underline;
}

#breadCrumb ol li a:hover {
text-decoration: none;	
}