// Header 
#headerContainer {
width: 100%;
float: left;
max-width: 100%;
min-width: 100%;
background-image: none;
background-color: $red;
}

#header {
width: 100%;
background-color: $red;
}


.headerTop {
min-width: 90%;
@media only screen and (min-width : $desktop) {
max-width: 940px;
min-width: 940px;
margin: 0 auto;
padding: 0;
}
margin-left: 5%;
margin-right: 5%;
}

#headerLogo {
    margin: 0;
    @media only screen and (min-width : $desktop) {
     margin: 0 0 .5em 0;
    }
}

#headerLogo a {
text-decoration: none;
}

h1.offCanvasHeaderLogo {
font-size: 14px !important;	
}

#headerLogo, #offCanvasHeaderLogo a {
float: left;
text-decoration: none;
}


#navigationContainer {
height: 33px;	
}

.navDropdown {
font-size: 1.1em !important;
}

@media only screen and (max-width: 979px) {
/*
#navigationContainer, .headerLogo {
display: none;
// Hides HSE logo in mobile view  
}
*/
} 

// Search box 

#globalSearch, #globalSearchMobile  {
float: left;
}

.desktop-gcs-search {
@media only screen and (min-width : $desktop) {
display: block;
}
}

.mobile-gcs-search {
display: block;
width: 90%;
margin: 0 auto;
@media only screen and (min-width : $desktop) {
display: none
}   
}

.mobile-gcs-search .gsc-input-box {
height: 37px;
}

.mobile-gcs-search #gsc-i-id2 {
margin: 0 !important;
height: 37px !important;
}

.gsc-search-button {
padding-right: 0;
}

// Medium Devices, Desktops 
@media only screen and (max-width : 979px) {

#globalSearch table {
width: 100%;
}

#globalSearch {
width: 100% !important;
}

}

.navDrop {
display: none;
/* Hides drop-down menu */
}

.navDropdown li {
background: none;
}

// Main menu

.menuContainer {
width: 100%;
float: left;
margin-top: 20px;
background-color: $gray;
@media only screen and (min-width : $desktop) {
background-color: $red;
margin-bottom: 0;
}
}

/* ---------------------------- */
/* Main menu */ 
/* ---------------------------- */

@media only screen and (min-width : $desktop) {

.menu li.search {
display: none;
}


.home .menu li.home,
.news .menu li.news,
.guidance .menu li.guidance,
.aboutyou .menu li.aboutyou,
.abouthse .menu li.abouthse,
.contact .menu li.contact, .freeUpdates .menu li.freeUpdates  {
	background-color:#333; /*changed by Sarah 26/2/19 from #59031b*//* add darker background colour for selected tab */
	border-radius:0px; /*changed by Sarah from 5px 5px 0 0 20/2/19*/
}

.home .menu li.home > a,
.home .menu li.home + li.news > a,
.news .menu li.news > a,
.news .menu li.news + li.guidance > a,
.guidance .menu li.guidance > a,
.guidance .menu li.guidance + li.aboutyou > a,
.aboutyou .menu li.aboutyou > a,
.aboutyou .menu li.aboutyou + li.abouthse > a,
.abouthse .menu li.abouthse > a,
.abouthse .menu li.abouthse + li.contact > a { 
    background-image:none; /* remove seperator background image on selected and adjacent tabs */
} 

}

// Cookie container 
#cookieContainer {
width: 90% !important;
padding: 10px 0;
padding-left: 5%;
padding-right: 5%;
@media only screen and (min-width : $desktop) {
width: 100% !important;
padding-left: 0;
padding-right: 0;
}
}